const gotoElements = document.querySelectorAll("a.goto") as NodeListOf<HTMLAnchorElement>
console.log(gotoElements.length)
gotoElements.forEach( gotoElement =>
{
    gotoElement.addEventListener("click", (evt: MouseEvent) =>
    {

        evt.preventDefault()

        const urlParams = new URLSearchParams(gotoElement.href)
        const engine = urlParams.get("engine")

        if(!engine)
            return

        console.log(engine)
        window.location.assign(`${engine}.html`)
    }   )
})
